<template>
  <div class = "modal-choice">

    <div
    v-if="modalInfo.head"
    class="modal-choice-header">
      {{modalInfo.head}}
    </div>

    <div 
    v-if="modalInfo.body"
    v-html="modalInfo.body"
    class="modal-choice-body">

    </div>

    <div
    v-if="modalInfo.footer"
    class="modal-choice-footer">
      <div
      class="modal-choice-button"
      v-for="(item, index) in modalInfo.footer.btnData"
      :key="index"
      @click="buttonAction(item.action)"
      >
        <div class="modal-choice-button-text">{{item.name}}</div>

      </div>

    </div>

  </div>
</template>

<script>

export default {
    props: {
      modalInfo: {},
      qualityReception: Number,
      doctorComfort: Number
    },
    methods: {
      buttonAction(action) {
        this.$emit("action", action)
      }
    }
}
</script>

