<template>
  <div class="questions box" style="display: flex">
    
    <div class="header">
      <img src="../assets/images/chat_logo.svg">
      <div class="title">
        Выберите вариант:
      </div>
    </div>
        
    <div 
      v-if="actualState.type === 'answer'"
      class="body"        
      >
      <div
        v-for="(item, index) in answers"
        :key="index"
        @click = actionAnswer(index,item)
        class="question"
        :class="disableAnswers"
      >
      {{item.text}}
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    actualState: {},
    answers: [],
    disableAnswers: String
  },
  methods: {
    actionAnswer(index, item) {      
      this.$emit("actionanswer", item.action)
      
      if (item.deleteAnswer) {
        this.$emit("deleteanswer", index)
      }
    }
  }
}
</script>

<style>
.disable-answer {
  pointer-events:none;
}
</style>