<template>

<div class = "new-final-progress">

    <div
    v-if="actualStateData.head"
    class="new-final-header">
    {{actualStateData.head}}
    </div>

    <div class="new-final-main">
        <div class="new-final-item">
            <div class="new-final-image-container">
                <div class="new-final-image-wrapper">
                    <img src="@/assets/images/progress_patient_woman.svg" class="new-final-image">
                </div>
            </div>
            <div class="new-final-indicators">
                <div class="new-final-name">Комфорт пациента</div>
                <div class="new-final-line">
                    <progress
                    class="new-final-progress-bar"
                    :value="qualityReception"
                    max="100">
                    </progress>
                </div>
            </div>
        </div>
        <div class="new-final-item">
            <div class="new-final-image-container">
                <div class="new-final-image-wrapper">
                    <img src="@/assets/images/progress_doctor_man.svg" class="new-final-image">
                </div>
            </div>
            <div class="new-final-indicators">
                <div class="new-final-name">Комфорт врача</div>
                <div class="new-final-line">
                    <progress
                    class="new-final-progress-bar"
                    :value="doctorComfort"
                    max="100">
                    </progress>
                </div>
                
            </div>
        </div>
    </div>

    <div 
    v-if="actualStateData.body"    
    class="new-final-body">
        <div
        v-html="actualStateData.body"
        class="new-final-body-text"
        >

        </div>
    </div>

    <div
    v-if="actualStateData.footer"
    class="new-final-footer">

        <div
        class="new-final-button"
        v-for="(item, index) in actualStateData.footer.btnData"
        :key="index"
        @click="buttonAction(item.action)"
        >
            {{item.name}}

        </div>
    </div>
</div>

