<template>
  <div class="content"
  v-if="actualState.showChat!==false"
  >
    <div 
    :class = "{'top-progress-hide-chat': actualState.hideChatQuestions}"
    class="chart-wrapper">

      <!-- <TopProgress
        :qualityReception="qualityReception"
        :doctorComfort="doctorComfort"
      >

      </TopProgress> -->

      <NewTopProgress
        :qualityReception="qualityReception"
        :doctorComfort="doctorComfort"
        :hideChatQuestions="actualState.hideChatQuestions"
      >

      </NewTopProgress>
      
      <div
      v-if="!actualState.hideChatQuestions"      
      class="chart box" ref="chartbox">
        <ChatMessage
          v-for="(item, index) in chatList" :key="index"
          :message = chatList[index]
          @scrolldown="scrollDown"
        />
          
        <ImageModal
          v-if="actualState.data.chatImage"
          :chatImage = actualState.data.chatImage
        />        
    </div>

    </div>

    <ChatQuestions
      v-if="!actualState.hideChatQuestions"
      :actualState = actualState
      :answers = answers
      :disableAnswers = "disableAnswers"
      @actionanswer = actionAnswer($event)
      @deleteanswer = deleteAnswer($event)
      />
  </div>

</template>

<script>
import ChatMessage from "@/components/ChatMessage.vue"
import ChatQuestions from "@/components/ChatQuestions.vue"
import ImageModal from '@/components/ImageModal.vue'
// import TopProgress from '@/components/TopProgress.vue'
import NewTopProgress from '@/components/NewTopProgress.vue'

export default {
  components: {
    ChatMessage,
    ChatQuestions,
    ImageModal,
    // TopProgress,
    NewTopProgress
  },
  props: {
    chatList: [],
    actualState: {},
    answers: [],
    disableAnswers: String,
    qualityReception: Number,
    doctorComfort: Number
  },
  methods: {
    actionAnswer(action) {
      this.$emit("actionanswer", action)
    },
    deleteAnswer(index) {
      this.$emit("deleteanswer", index)
    },
    scrollDown() {
      this.$refs.chartbox.scrollTop = this.$refs.chartbox.scrollHeight
    }    
  }
}
</script>

